.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo, .header__menu, .header__list {
  display: flex;
}

.header__item {
  transition: color .3s;
  position: relative;
}

.header__link {
  color: #060b35;
  text-transform: uppercase;
  flex-wrap: wrap;
  align-content: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.6px;
  display: flex;
}

.header__item:not(:last-child) {
  margin-right: 20px;
}

.header__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  cursor: default;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  max-width: 200px;
  transition: all .3s;
}

.card__container {
  flex-direction: column;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  justify-content: center;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__photo--margin {
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title--margin {
  margin-bottom: 4px;
}

.card__subtitle--margin {
  margin-bottom: 16px;
}

.card__subtitle {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  height: 40px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  transition: background-color .3s linear, color .3s linear;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(-n) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--0 .stars__star:nth-child(n+1) {
  background-image: url("star.822d561e.svg");
}

.stars--1 .stars__star:nth-child(-n+1) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--1 .stars__star:nth-child(n+2) {
  background-image: url("star.822d561e.svg");
}

.stars--2 .stars__star:nth-child(-n+2) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--2 .stars__star:nth-child(n+3) {
  background-image: url("star.822d561e.svg");
}

.stars--3 .stars__star:nth-child(-n+3) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--3 .stars__star:nth-child(n+4) {
  background-image: url("star.822d561e.svg");
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

.stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--5 .stars__star:nth-child(n+6) {
  background-image: url("star.822d561e.svg");
}

.page {
  justify-content: center;
  display: flex;
}

.page__container {
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.price--margin {
  margin-bottom: 16px;
}

.feedback {
  justify-content: space-between;
  display: flex;
}

.feedback__review {
  color: #060b35;
  align-self: center;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.feedback--margin {
  margin-bottom: 24px;
}

* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

ul, ol, li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  font-family: Roboto, sans-serif;
}

@media (width >= 488px) {
  .page__container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .page__container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .page__container {
    grid-template-columns: repeat(4, 1fr);
  }
}

